const columnsDimensionPercent = [
  {
    title: 'Nombre de la dimensión',
    key: 'name',
  },
  {
    title: '% Ponderacion',
    key: 'percent',
    render: text => `${text}%`,
  },
];

export default columnsDimensionPercent;



