const columnsCriterionPercent = [
  {
    title: 'Nombre del criterio',
    key: 'name',
  },
  {
    title: '% Ponderacion',
    key: 'percent',
    render: text => `${text}%`,
  },
];

export default columnsCriterionPercent;



